interface RussiaMapProps {
    sx?: object;  //    sx
}

const RussiaMap: React.FC<RussiaMapProps> = ({ sx }) => {
    const [hoveredRegion, setHoveredRegion] = useState<string | null>(null)
    const [open, setOpen] = useState<boolean>(false);
    const [selectedRegion, setSelectedRegion] = useState<string | null>(null);
    const handleRegionClick = (regionId: string) => {
        setSelectedRegion(regionId);
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <Box display="flex" justifyContent="center" alignItems="center" width="100%" height="100%" overflow="visible" sx={sx}>
            <svg
                viewBox="0 0 1000 600"
                className="w-full h-auto"
                xmlns="http://www.w3.org/2000/svg"
                overflow={"visible"}
            >
                {regions.map((region) => (
                    <g
                        key={region.id}
                        onMouseEnter={() => setHoveredRegion(region.id)}
                        onMouseLeave={() => setHoveredRegion(null)}
                        onClick={() => handleRegionClick(region.id)} //  PopUp  
                        style={{
                            transform: hoveredRegion === region.id ? "scale(1.2) translateX(-20px)" : "translateX(-20px)",
                            transformOrigin: "center",
                            transition: "transform 0.5s ease-in-out",
                            cursor: 'pointer', //  
                        }}
                    >
                        <path
                            d={region.d}
                            fill={region.fill}
                            stroke="white"
                            strokeWidth="2"
                        />
                    </g>
                ))}
            </svg>

            {/*   */}
            <PopUp
                open={open}
                onClose={handleClose}
                selectedRegion={selectedRegion}
            />
        </Box>
    )
}

export default RussiaMap

